@import url(https://fonts.googleapis.com/css2?family=Delius+Swash+Caps&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{background-image:url(/static/media/bg2.e6b35c2c.jpg);background-size:cover;height:100vh;color:white}body h1{font-family:"Delius Swash Caps", cursive;margin:1rem;text-align:center}@media screen and (max-width: 500px){body{background-image:url(/static/media/bg2small.fe137a85.jpg)}}.app{display:flex;flex-direction:column;align-items:center;margin-top:1.5rem}.game-container{-webkit-perspective:1000px;perspective:1000px;display:grid;grid-template-columns:repeat(4, auto);grid-gap:5px;gap:5px;width:90vw;height:calc(90vw * 1.14);max-height:467px;max-width:410px}.game-container .card{position:relative;transition:-webkit-transform 0.5s ease-in-out;transition:transform 0.5s ease-in-out;transition:transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;-webkit-transform:scale(1);transform:scale(1);cursor:pointer}.game-container .card .front-face,.game-container .card .back-face{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;border-radius:10px}.game-container .card .front-face{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.game-container .flip{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}@media screen and (min-width: 700px) and (min-height: 1000px){.game-container{max-height:700.5px;max-width:615px}}.score-container{display:flex;flex-wrap:wrap;font-family:"Delius Swash Caps", cursive;width:90vw;max-width:450px;justify-content:space-between;align-items:center;margin-bottom:1.5rem}.score-container .timer{text-align:center;width:100%;margin-bottom:1.5rem}.MuiDialogActions-root{justify-content:center !important}

